import React, { useState, useEffect } from "react";
import { Link, Redirect } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBIcon,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step3(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();
    setErrorMsg(!isValid ? "Please Fill out all fields Properly" : "");
      
  };

  useEffect(() => {
    if (formRef.current !== null)
    setTouched(formRef.current.checkValidity());
  }, []);

    if (state.relationshipStatus !== "Married") return <Redirect to="/start/11" />;

  return (
    <Step heading={`Does your spouse have student loans?`}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <MDBRow center>
          <MDBCol sm={10} md={8}>
            <div className="my-4" />
            <MDBRow center>
              <MDBCol md={6} lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "spouseStudentLoan", "Yes");
                  }}
                >
                  Yes
                </MDBBtn>
              </MDBCol>
              <MDBCol className="d-block d-lg-none my-2"></MDBCol>
              <MDBCol md={6} lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "spouseStudentLoan", "No");
                    history.push(nextStep);
                  }}
                >
                  No
                </MDBBtn>
              </MDBCol>
            </MDBRow>

            {state.spouseStudentLoan == "Yes" && (
              <>
                <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
                  <MDBRow className="mt-5">
                    <MDBCol center md={12}>
                      <h4>Please Provide the Following Details</h4>
                    </MDBCol>

                    <MDBCol sm={12}>
                      <MDBInput
                        label="SSN"
                        group
                        type="tel"
                        name="spouseSSN"
                        value={state.spouseSSN}
                        onChange={(e) => {
                          if (e.target.value.length > 11) return;
                          onChangeField(
                            e,
                            e.target.name,
                            normalizeInput(e.target.value, state.spouseSSN)
                          );
                          // formRef.current.phone.value = normalizeInput(state.phone);
                          validate();
                        }}
                        pattern="\d{3}-\d{2}-\d{4}"
                        required
                        containerClass="mb-0"
                      />
                    </MDBCol>
                    <MDBCol  sm={12}>
                      <MDBInput
                        label="Spouse Name"
                        group
                        type="text"
                        pattern="^[a-zA-Z ][a-zA-Z\\s ]+$"
                        name="spouseName"
                        value={state.spouseName}
                        onChange={(e) => {
                          if (e.target.value.length > 11) return;
                          onChangeField(e, e.target.name, e.target.value);
                          // formRef.current.phone.value = normalizeInput(state.phone);
                          validate();
                        }}
                        required
                        containerClass="mb-0"
                      />
                    </MDBCol>
                    <MDBCol  sm={12} className="mt-3">
                      <MDBInput
                        label="Spouse DOB"
                        group
                        type="date"
                        name="spouseDOB"
                        value={state.spouseDOB}
                        onChange={(e) => {
                          if (e.target.value.length > 11) return;
                          onChangeField(e, e.target.name, e.target.value);
                          // formRef.current.phone.value = normalizeInput(state.phone);
                          validate();
                        }}
                        required
                        containerClass="mb-0"
                      />
                    </MDBCol>
                  </MDBRow>
                </form>
                <MDBRow
                  center
                  className="d-flex flex-column align-items-center"
                >
                  {touched && errorMsg.length > 0 && (
                    <div className="text-danger mb-2">{errorMsg}</div>
                  )}
                  {touched && !errorMsg && (
                    <Link to={nextStep}>
                      <MDBBtn
                        color="pink"
                        rounded
                        type="button"
                        className="z-depth-1a"
                      >
                        Next
                      </MDBBtn>
                    </Link>
                  )}
                  {(!touched || errorMsg) && (
                    <MDBBtn
                      color={"grey"}
                      disabled
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  )}
                </MDBRow>
              </>
            )}
          </MDBCol>
        </MDBRow>
      </MDBContainer>
    </Step>
  );
}

const normalizeInput = (value, previousValue) => {
  if (!value) return value;
  const currentValue = value.replace(/[^\d]/g, "");
  // xxx-xx-xxxx
  // 012-34-5678
  if (!previousValue || value.length > previousValue.length) {
    if (currentValue.length <= 3) return currentValue;
    if (currentValue.length === 3) return `${currentValue}-`;
    if (currentValue.length <= 5)
      return `${currentValue.slice(0, 3)}-${currentValue.slice(3)}`;
    if (currentValue.length === 5)
      return `${currentValue.slice(0, 3)}-${currentValue.slice(3)}-`;
    return `${currentValue.slice(0, 3)}-${currentValue.slice(
      3,
      5
    )}-${currentValue.slice(5)}`;
  }
};
