import React, { Component } from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import axios from "axios";
import Step1 from "./Step1";
import Step2 from "./Step2";
import Step3 from "./Step3";
import Step4 from "./Step4";
import Step5 from "./Step5";
import Step6 from "./Step6";
import Step7 from "./Step7";
import Step8 from "./Step8";
import Step9 from "./Step9";
import Step10 from "./Step10";
import Step11 from "./Step11";
import Step12 from "./Step12";
import Step13 from "./Step13";
import Step14 from "./Step14";
import Step15 from "./Step15";
import Step16 from "./Step16";
import Step17 from "./Step17";
import Step18 from "./Step18";
import Step19 from "./Step19";
import Step20 from "./Step20";
import Step21 from "./Step21";
import Step22 from "./Step22";
import Step23 from "./Step23";
import Step24 from "./Step24";
import Step25 from "./Step25";
import Step26 from "./Step26";
// import AllOptions from "./AllOptions";
import ThankYou from "./ThankYou";
import Decline from "./Decline";
export default class MultiStepForm extends Component {
  state = {
    // step1
    firstName: "",
    lastName: "",

    //step 2
    goal: "",

    //step3
    latLng: {},
    streetAddress: "",
    aptUnitNo: "",
    city: "",
    state: "",
    zipCode: "",

    // step4
    phone: "",

    // step5
    email: "",

    // step6
    loanInDeferment: "",

    // step7
    noOfSupportChildren: "",
    noOfsupportPeople: "",

    // step8
    relationshipStatus: "",

    // step9
    spouseStudentLoan: "",
    spouseSSN: "",
    spouseName: "",
    spouseDOB: "",

    // step10
    filedTaxes: "",

    // step11
    businessEstablishmentDate: "",

    // step12
    ownershipOfBusinessDate: "",

    focus: "",
    creditCardNumber: "",
    exp: "",
    cvv: "",
    nameOnCard:"",

    // step25
    agree: "",
  };
  handleChangeField = (e, name, value) => {
    this.setState(
      {
        [name]: value,
      },
      () => console.log(this.state)
    );
  };

  submitData() {
    const data = {
      firstName: this.state.firstName,
      lastName: this.state.lastName,
      latLng: this.state.latLng,
      streetAddress: this.state.streetAddress,
      aptUnitNo: this.state.aptUnitNo,
      city: this.state.city,
      state: this.state.state,
      zipCode: this.state.zipCode,
      phone: this.state.phone,
      email: this.state.email,
      loanInDeferment: this.state.loanInDeferment,
      noOfSupportChildren: this.state.noOfSupportChildren,
      noOfsupportPeople: this.state.noOfsupportPeople,
      relationshipStatus: this.state.relationshipStatus,
      spouseStudentLoan: this.state.spouseStudentLoan,
      spouseSSN: this.state.spouseSSN,
      spouseName: this.state.spouseName,
      spouseDOB: this.state.spouseDOB,
      filedTaxes: this.state.filedTaxes,

      agree: this.state.agree,
    };

    alert(JSON.stringify(data));
    //  axios
    //    .post(
    //      "https://hooks.zapier.com/hooks/catch/1712527/o5y1jta/",
    //      data
    //    )
    //    .then((response) => console.log(response))
    //    .finally(() => console.log("Data Sent", { data: data }));
  }

  renderStep = (props) => {
    const { history } = props;
    const { path, params } = props.match;
    if (params.step === "thankyou") {
      this.submitData();
      return <ThankYou />;
    } else if (params.step === "decline") {
      return <Decline />;
    }
    const step = parseInt(params.step);
    const currentPath = `/${path.split("/")[1]}`;
    switch (step) {
      case 1:
        return (
          <Step1
            history={history}
            nextStep={`${currentPath}/2`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 2:
        return (
          <Step2
            history={history}
            prevStep={`${currentPath}/1`}
            nextStep={`${currentPath}/3`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 3:
        console.log("step 3");

        return (
          <Step3
            history={history}
            prevStep={`${currentPath}/2`}
            nextStep={`${currentPath}/4`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 4:
        console.log("step 4");
        return (
          <Step4
            history={history}
            prevStep={`${currentPath}/3`}
            nextStep={`${currentPath}/5`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 5:
        return (
          <Step5
            history={history}
            prevStep={`${currentPath}/4`}
            nextStep={`${currentPath}/6`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 6:
        return (
          <Step6
            history={history}
            prevStep={`${currentPath}/5`}
            nextStep={`${currentPath}/7`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 7:
        return (
          <Step7
            history={history}
            prevStep={`${currentPath}/6`}
            nextStep={`${currentPath}/8`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 8:
        return (
          <Step8
            history={history}
            prevStep={`${currentPath}/7`}
            nextStep={`${currentPath}/9`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 9:
        return (
          <Step9
            history={history}
            prevStep={`${currentPath}/8`}
            nextStep={`${currentPath}/10`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 10:
        return (
          <Step10
            history={history}
            prevStep={`${currentPath}/9`}
            nextStep={`${currentPath}/11`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 11:
        return (
          <Step11
            history={history}
            prevStep={`${currentPath}/10`}
            nextStep={`${currentPath}/12`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 12:
        return (
          <Step12
            history={history}
            prevStep={`${currentPath}/11`}
            nextStep={`${currentPath}/13`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 13:
        return (
          <Step13
            history={history}
            prevStep={`${currentPath}/12`}
            nextStep={`${currentPath}/14`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
     
      case 14:
        return (
          <Step26
            history={history}
            prevStep={`${currentPath}/13`}
            nextStep={`${currentPath}/thankyou`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      default:
        return <div>invalid step</div>;
    }
  };
  render() {
    const { path } = this.props.match;
    return (
      <Switch>
        <Route
          path="/start"
          exact
          render={() => <Redirect to={`${path}/1`} />}
        />
        <Route
          path={`${path}/:step`}
          render={(props) => (
            <>
              {this.renderStep(props)}
              <small
                style={{
                  position: "absolute",
                  width: "100%",
                  bottom: 0,
                  textAlign: "center",
                  padding: 10,
                }}
              ></small>
            </>
          )}
        />
      </Switch>
    );
  }
}
