import React from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBIcon,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
import { Redirect } from "react-router-dom"

export default function Step3(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;


    return (
      <Step heading={`What's your current relationship Status?`}>
        <img
          src={BackBtn}
          className="btn-back"
          onClick={() => history.push(prevStep)}
        />
        <MDBContainer>
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              <div className="my-4" />
              <MDBRow center>
                <MDBCol md={6} lg={4}>
                  <MDBBtn
                    color="pink"
                    rounded
                    type="button"
                    className="btn-block z-depth-1a"
                    onClick={(e) => {
                      onChangeField(e, "relationshipStatus", "Single");
                      history.push(nextStep);
                    }}
                  >
                    Single
                  </MDBBtn>
                </MDBCol>
                <MDBCol className="d-block d-lg-none my-2"></MDBCol>
                <MDBCol md={6} lg={4}>
                  <MDBBtn
                    color="pink"
                    rounded
                    type="button"
                    className="btn-block z-depth-1a"
                    onClick={(e) => {
                      onChangeField(e, "relationshipStatus", "Married");
                      history.push(nextStep);
                    }}
                  >
                    Married
                  </MDBBtn>
                </MDBCol>
                <MDBCol className="d-block d-lg-none my-2"></MDBCol>
                <MDBCol md={6} lg={4}>
                  <MDBBtn
                    color="pink"
                    rounded
                    type="button"
                    className="btn-block z-depth-1a"
                    onClick={(e) => {
                      onChangeField(
                        e,
                        "relationshipStatus",
                        "Married, But Separated"
                      );
                      history.push(nextStep);
                    }}
                  >
                    Married, But Seperated
                  </MDBBtn>
                </MDBCol>
              </MDBRow>
            </MDBCol>
          </MDBRow>
        </MDBContainer>
      </Step>
    );
}
